.movie_player {
  width: 100vw;
}

.video_player {
  width: 80%;
  height: 600px;
  cursor: pointer;
  display: block;
  margin: auto;
}

@media screen and (max-width: 370px) {
  .video_player {
    width: 90%;
    height: 200px;
  }
}

@media screen and (min-width: 371px) and (max-width: 576px) {
  .video_player {
    width: 90%;
    height: 250px;
  }
}

@media screen and (min-width: 577px) and (max-width: 768px) {
  .video_player {
    width: 90%;
    height: 300px;
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .video_player {
    width: 90%;
    height: 500px;
  }
}

