.movie_slider_container {
  width: 100vw;
  position: relative;
}

.movie_slider_container img {
  width: 100%;
  height: 500px;
}

.sliding_movie_details {
  width: 60%;
  margin: auto;
  position: absolute;
  top: 70%;
  left: 50%;
  translate: -50% -50%;
  font-family: var(--font-poppins);
  color: white;
  background-color: transparent;
}
.sliding_movie_details span {
  display: flex;
  gap: 19%;
}
.sliding_movie_details p {
  font-weight: normal;
  font-size: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Change to number of lines you want to show */
  line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

@media screen and (max-width: 370px) {
  .movie_slider_container img {
    height: 250px;
  }

  .sliding_movie_details {
    width: 90%;
    top: 65%;
  }
  .sliding_movie_details span {
    gap: 30%;
  }
  .sliding_movie_details p {
    font-weight: normal;
    font-size: 0.75rem;
  }
}

@media screen and (min-width: 371px) and (max-width: 576px) {
  .movie_slider_container img {
    height: 350px;
  }

  .sliding_movie_details {
    width: 90%;
    top: 70%;
  }
  .sliding_movie_details span {
    gap: 25%;
  }
  .sliding_movie_details p {
    font-size: 0.875rem;
  }
}

@media screen and (min-width: 577px) and (max-width: 768px) {
  .movie_slider_container img {
